.top {
  position: fixed;
  display: flex;
  top: 0px;
  width: 100vw;
  height: 70px;
  background-color: rgb(28, 28, 28);
  align-items: center;
}

.logo {
  margin-left: 20px;
  font-size: x-large;
  font-family: "Arvo", serif;
  color: #5cb492;
}

.mainBox {
  position: fixed;
  width: 100vw;
  height: calc(100vh - 70px);
  top: 70px;
  overflow-y: scroll;
}
